@font-face {
  font-family: "Inter Regular";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/inter-regular.woff2") format("woff2");
}
@font-face {
  font-family: "Inter Medium";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/inter-medium.woff2") format("woff2");
}
@font-face {
  font-family: "Inter Semi-bold";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/inter-semibold.woff2") format("woff2");
}
@font-face {
  font-family: "Inter Bold";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/inter-bold.woff2") format("woff2");
}
html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  min-width: 1440px;
  font-family: "Inter Regular", "Arial", sans-serif;
  font-size: 18px;
  line-height: 28px;
  color: #6B7280;
  background-color: #FFFFFF;
}

img {
  max-width: 100%;
  height: auto;
}

.main-container {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-header {
  margin-top: 80px;
  margin-bottom: 180px;
}

.navigation {
  width: 1180px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navigation-logo {
  flex-shrink: 0;
  margin-right: 80px;
}

.navigation-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
}

.navigation-item:not(:last-of-type) {
  overflow: hidden;
  margin-right: 80px;
  margin-bottom: 30px;
}

.navigation-link {
  text-decoration: none;
  padding: 15px 0;
  color: #6B7280;
}
.navigation-link span {
  position: relative;
  display: inline-block;
  transition: transform 0.3s;
}
.navigation-link span::before {
  position: absolute;
  top: 100%;
  content: attr(data-hover);
  color: #3730A3;
  transform: translate3d(0, 0, 0);
}
.navigation-link:hover span {
  transform: translateY(-100%);
}

.button {
  text-decoration: none;
  font-family: inherit;
  color: #3730A3;
  padding: 15px 30px;
  border: 2px solid #3730A3;
  border-radius: 6px;
  transition: 0.3s;
}
.button:hover {
  background-color: #EEF2FF;
}

.button-dark {
  color: #FFFFFF;
  background-color: #3730A3;
  transition: 0.3s;
}
.button-dark:hover {
  color: #3730A3;
  background-color: #EEF2FF;
}

.about {
  width: 1180px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 200px;
}

.about-wrapper {
  margin-right: 80px;
}

.about-name {
  color: #3730A3;
  padding-left: 110px;
  margin-bottom: 20px;
  display: block;
  position: relative;
}
.about-name::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 100px;
  height: 1px;
  background-color: #3730A3;
}

.about-title {
  font-size: 72px;
  line-height: 72px;
  font-family: "Inter Bold", "Arial", sans-serif;
  color: #1F2937;
  display: block;
  padding-bottom: 40px;
}

.about-description {
  margin: 0;
  padding-bottom: 50px;
  padding-right: 50px;
}

.about-buttons {
  display: flex;
  margin-bottom: 60px;
}

.about-button {
  margin-right: 20px;
}

.about-note {
  font-size: 12px;
  line-height: 16px;
  position: relative;
  margin: 0;
}
.about-note::before {
  position: absolute;
  content: "";
  bottom: 30px;
  width: 100%;
  height: 1px;
  background-color: #E5E7EB;
}

.about-image {
  min-width: 478px;
}

.services {
  width: 1180px;
  margin: 0 auto;
  padding: 0 20px;
  margin-bottom: 200px;
}

.services-title {
  font-size: 48px;
  line-height: 48px;
  color: #1F2937;
  text-align: center;
  display: block;
  padding-left: 80px;
  padding-right: 80px;
  padding-bottom: 200px;
}

.services-accent {
  color: #3730A3;
}

.services-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  padding-left: 490px;
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fit, 310px);
  row-gap: 60px;
  column-gap: 70px;
}
.services-list::before {
  position: absolute;
  content: "";
  left: 0;
  top: -40px;
  width: 440px;
  height: 622px;
  background-image: url("../img/image-1.jpg");
  background-color: #cccccc;
}

.services-name {
  margin: 0;
  margin-bottom: 20px;
  font-family: "Inter Semi-bold", "Arial", sans-serif;
  color: #1F2937;
}

.services-description {
  margin: 0;
  margin-bottom: 20px;
  font-size: 12px;
  line-height: 18px;
}

.services-link {
  text-decoration: none;
  position: relative;
  display: inline-block;
  padding-right: 25px;
  font-size: 12px;
  line-height: 16px;
  color: #3730A3;
}
.services-link::after {
  position: absolute;
  content: "";
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 16px;
  height: 16px;
  background-image: url("../img/arrow-right.svg");
}
.services-link::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #3730A3;
  top: 100%;
  left: 0;
  pointer-events: none;
  transform-origin: 100% 50%;
  transform: scale3d(0, 1, 1);
  transition: transform 0.3s;
}
.services-link:hover::before {
  transform-origin: 0% 50%;
  transform: scale3d(1, 1, 1);
}

.partners {
  margin-bottom: 150px;
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: #FEF2F2;
}

.partners-wrapper {
  width: 1180px;
  margin: 0 auto;
  padding: 0 20px;
}

.partners-text {
  font-size: 20px;
  line-height: 28px;
  color: #9CA3AF;
  display: block;
  margin-bottom: 20px;
}

.partners-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
}

.partners-item:not(:nth-child(5n)) {
  margin-right: 97px;
}

.partners-link:hover .partners-logo {
  fill: #3730A3;
}

.partners-logo {
  fill: #B4A6A2;
  transition: 0.3s;
}

.projects {
  width: 1180px;
  margin: 0 auto;
  padding: 0 20px;
  margin-bottom: 150px;
}

.projects-title {
  font-size: 48px;
  line-height: 48px;
  color: #1F2937;
  text-align: center;
  display: block;
  margin-right: 80px;
  margin-left: 80px;
  margin-bottom: 150px;
}

.projects-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-areas: "project-1 project-2" "project-3 project-2" "project-4 project-4";
  gap: 25px;
}
.projects-list img {
  vertical-align: middle;
}

.projects-item {
  position: relative;
}
.projects-item:hover .projects-info {
  opacity: 1;
}

.project-1 {
  grid-area: project-1;
}

.project-2 {
  grid-area: project-2;
}

.project-3 {
  grid-area: project-3;
}

.project-4 {
  grid-area: project-4;
}

.projects-info {
  opacity: 0;
  padding: 20px 25px;
  box-sizing: border-box;
  position: absolute;
  transition: 0.3s;
  bottom: 20px;
  left: 20px;
  width: calc(100% - 40px);
  background-color: #FFFFFF;
}
.projects-info__open {
  opacity: 1;
}

.projects-link {
  text-decoration: none;
  display: block;
  position: relative;
  color: #1F2937;
}
.projects-link::after {
  position: absolute;
  content: "";
  right: 0;
  top: 50%;
  width: 30px;
  height: 30px;
  transform: translateY(-50%);
  background-image: url("../img/arrow-right-big.svg");
}

.projects-type {
  display: block;
  font-size: 14px;
  line-height: 20px;
  color: #9CA3AF;
}

.planning {
  width: 1180px;
  margin: 0 auto;
  padding: 0 20px;
  margin-bottom: 150px;
}

.planning-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 340px);
  gap: 80px;
  text-align: center;
}

.planning-title {
  position: relative;
  margin: 0;
  margin-bottom: 20px;
  padding-top: 180px;
  min-width: 140px;
  font-size: 30px;
  line-height: 36px;
  color: #1F2937;
}
.planning-title::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 140px;
  height: 140px;
  background-color: #EEF2FF;
  background-image: url("../img/bar-chart.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.planning-title__finance::before {
  background-image: url("../img/dollar-sign.svg");
}
.planning-title__analytics::before {
  background-image: url("../img/search.svg");
}

.planning-description {
  margin: 0;
}

.careers {
  width: 1180px;
  margin: 0 auto;
  padding: 0 20px;
  margin-bottom: 150px;
}

.careers-title {
  font-size: 48px;
  line-height: 48px;
  color: #1F2937;
  text-align: center;
  margin: 0;
  margin-bottom: 150px;
}

.careers-table {
  width: 100%;
  font-size: 16px;
  line-height: 24px;
}
.careers-table th {
  text-transform: uppercase;
  text-align: left;
  padding-bottom: 80px;
}
.careers-table th:not(:last-of-type) {
  padding-right: 30px;
}
.careers-table td:not(:last-of-type) {
  padding-right: 30px;
}
.careers-table tr:not(:last-of-type) td {
  padding-bottom: 80px;
}
.careers-table b {
  font-family: "Inter Semi-bold", "Arial", sans-serif;
}

.page-footer {
  background-color: #FEF2F2;
}

.contacts {
  width: 1180px;
  margin: 0 auto;
  padding: 0 20px;
  padding: 110px 20px;
}

.contacts-title {
  font-size: 48px;
  line-height: 48px;
  color: #1F2937;
  text-align: center;
  margin: 0;
  margin-bottom: 20px;
}

.contacts-text {
  text-align: center;
  margin: 0;
  margin-bottom: 100px;
  margin-left: 250px;
  margin-right: 250px;
}

.contacts-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  max-width: 460px;
  margin: 0 auto;
}

.contacts-item:not(:nth-child(6n)) {
  margin-right: 50px;
}

.contacts-link svg {
  stroke: #6B7280;
  stroke-width: 3px;
  transition: 0.3s;
}

.contacts-link:hover svg {
  stroke: #3730A3;
}